import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import axios from 'axios'

function AlbumsList() {
    const [allAlbums, setAllAlbums] = useState([])

    useEffect(() => {
        axios.get("https://josuem65.github.io/json-photo-album/albums2.json").then((resp) => {
            let albumsList = resp.data.albums // added 'albums' to the path
            setAllAlbums(albumsList)
        })
    }, []) 

    return (
        <div className="albumsList">
            <h1 className="myAlbumsHeader">My Albums</h1>
            <div className="albumListContainer">
            {allAlbums.map((item) => {
                return (
                    <div className="albumThumbAndName" key={item.id}>
                        <Link to={"/album/" + item.id} className="albumThumbLink">
                            <img src={item.thumbnail} className="thumbnail"/>
                            <h4>{item.name} album</h4>
                        </Link>
                    </div>
                )
            })}
            </div>
        </div>
    )
}

export default AlbumsList;