import React, { useState, useEffect } from 'react'
import { Link, useParams } from 'react-router-dom'
import axios from 'axios'
import AlbumsList from './AlbumsList';

function Album() {
    const { id } = useParams()
    const [currentAlbum, setCurrentAlbum] = useState({photos: []});
    const [albumList, setAlbumList] = useState([])
    const [expandPhoto, setExpandPhoto] = useState(false)
    const [photoInfo, setPhotoInfo] =  useState(null)

    useEffect(() => {
        axios.get("https://josuem65.github.io/json-photo-album/albums2.json").then((resp) => {
            let album = resp.data.albums // added 'albums' to the path
            setCurrentAlbum(album[id - 1])
            // console.log(currentAlbum)
            setAlbumList(album)
        })
    }, [id])

    function handleClick(pictureDets) {
        setExpandPhoto(true)
        setPhotoInfo(pictureDets)
    }
    
    return(
        <div className="main">
        <div className={`photosPage ${expandPhoto ? "hide" : null}`}>
            <div className="selectedAlbumHeader">
                <h1 className="albumNameHeader">{currentAlbum.name} Album</h1>
                <Link to="/" className="leftArrowBox">Back to Albums</Link>
            </div>
            <div className="linksAndPhotos">
                <div className="albumsLinksContain">
                    {albumList.map((item) => {
                        return(
                        <Link to={"/album/" + item.id} key={item.id} className="albumLinks">{item.name} Album</Link>
                        )
                    })} 
                </div>
                <div className="photosListContainer">
                {currentAlbum.photos.map((item) => {
                    return(
                      <div className="photoDiv" onClick={() => handleClick(item)}>
                         <img src={item.url}/>
                         <h4>{item.name}</h4>
                      </div>
                    )
                })}
                </div>
            </div>
        </div>
        <div className="expandedPhotoContainer">
            {expandPhoto ? 
                <div className="expandedPhoto" onClick={() => setExpandPhoto(false)}>
                    <h1 onClick={(e) => e.stopPropagation()}>{photoInfo.name} </h1>
                    <img src={photoInfo.url} onClick={(e) => e.stopPropagation()}/>
                </div>
                :
                null
            }
        </div>
        </div>
    )
}


export default Album; 